import { FC, ReactNode } from 'react';
import styled from 'styled-components';

import {
  Flex,
  Tabs,
  TabsProps,
  Text,
  defaultThemeProp,
  ForwardProps
} from '@pega/cosmos-react-core';
import { StyledText } from '@pega/cosmos-react-core/lib/components/Text/Text';

export interface MobileCaseViewProps {
  title: string;
  tabs: TabsProps;
  tabContent: ReactNode;
}

export const StyledMobileCaseView = styled.div`
  min-height: 100vh;
  background-color: #e9eef3;
`;

export const StyledMobileCaseViewHeader = styled.header`
  color: ${props => props.theme.base.palette.light};
  background-color: ${props => props.theme.base.palette.interactive};

  ${StyledText} {
    padding: ${props => props.theme.base.spacing};
  }
`;

StyledMobileCaseView.defaultProps = defaultThemeProp;
StyledMobileCaseViewHeader.defaultProps = defaultThemeProp;

/** @deprecated */
const MobileCaseView: FC<ForwardProps & MobileCaseViewProps> = (props: MobileCaseViewProps) => {
  const { tabs, tabContent, title, ...restProps } = props;

  return (
    <Flex container={{ direction: 'column' }} as={StyledMobileCaseView} {...restProps}>
      <Flex
        container={{ direction: 'column' }}
        item={{ shrink: 0 }}
        as={StyledMobileCaseViewHeader}
      >
        <Text as='h1'>{title}</Text>
        <Tabs {...tabs} type='horizontal' inverted />
      </Flex>
      {tabContent}
    </Flex>
  );
};

export default MobileCaseView;
